import { ColorService } from './ColorService';
import { CanvasService } from './CanvasService';
import { PerspectiveService } from './PerspectiveService';

export class AnimationService {
  private static instance: AnimationService;
  private canvasDrawer: CanvasService;
  private perspectiveService: PerspectiveService;

  public static getInstance(): AnimationService {
    if (!this.instance) {
      this.instance = new AnimationService();
    }

    return this.instance;
  }

  public init() {
    this.canvasDrawer = CanvasService.getInstance();
    this.perspectiveService = PerspectiveService.getInstance();
    this.canvasDrawer.init();
    this.perspectiveService.init();
  }

  public startDrawing() {
    this.canvasDrawer.startDrawing();

    window.addEventListener('resize', () => this.perspectiveService.setCenter());
    window.addEventListener('mousemove', event => this.perspectiveService.updatePerspective(event));
  }
}
